.toolbar {
  color: var(--toolbar-font-color);
  align-items: center;
  background-color: var(--toolbar-background);
  box-shadow: 0 1px 0 var(--toolbar-border-color);
  display: flex;
  font-size: calc(15 / var(--rem-base) * 1rem);
  height: var(--toolbar-height);
  justify-content: flex-start;
  position: sticky;
  top: var(--navbar-height);
  z-index: var(--z-index-toolbar);
}

.toolbar a {
  color: inherit;
}

.nav-toggle {
  background: url(../img/menu.svg) no-repeat 50% 47.5%;
  background-size: 49%;
  border: none;
  outline: none;
  line-height: inherit;
  padding: 0;
  height: var(--toolbar-height);
  width: var(--toolbar-height);
  margin-right: -0.25rem;
}

.toolbar button.search-button {
  width: 200px;
  border-radius: 0;
  border-top: 0;
  border-bottom: 0;
  height: 40px;
  line-height: 40px;
  margin-left: 0.4rem;
}

.toolbar button.search-button:hover {
  box-shadow: none;
}

.toolbar button.search-button svg {
  top: 10px;
}

html.dark-theme .nav-toggle {
  background: url(../img/menu-dark.svg) no-repeat 50% 47.5%;
  background-size: 49%;
}

@media screen and (min-width: 1024px) {
  .nav-toggle,
  .toolbar {
    display: none;
  }
}

.nav-toggle.is-active {
  background-image: url(../img/back.svg);
  background-size: 41.5%;
}

html.dark-theme .nav-toggle.is-active {
  background-image: url(../img/back-dark.svg);
  background-size: 41.5%;
}

.home-link {
  display: block;
  background: url(../img/home-o.svg) no-repeat center;
  height: calc(var(--toolbar-height) / 2);
  width: calc(var(--toolbar-height) / 2);
  margin: calc(var(--toolbar-height) / 4);
}

.home-link:hover,
.home-link.is-current {
  background-image: url(../img/home.svg);
}
